/* Dots */
.slick-dots{
    position: absolute;
    bottom: 50px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    padding: 0 14px;

    li{
        position: relative;
        display: inline-block;
        padding: 0;
        cursor: pointer;
        margin: 0 2px;
        width: 39.5px;
        height: 4px;

        button{
            font-size: 0;
            line-height: 0;
            display: block;
            cursor: pointer;
            color: transparent;
            border: 0;
            outline: none;
            background: transparent;

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                text-align: center;
                color: black;
                background: rgba(255, 255, 255, 0.6);
            }
        }
    
    }
}
.slick-dots li:first-child button:before{
    border-radius: 2px 0 0 2px;
}
.slick-dots li:last-child button:before{
    border-radius: 0 2px 2px 0;
}
.slick-dots li.slick-active button:before{
    background: rgba(255, 255, 255, 1);

}
.slick-dots li button:hover,
.slick-dots li button:focus,
button:focus{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before{
    opacity: 1;
}
.slick-list:focus,
.slick-slider:focus{
    outline: none;
}

